	module.exports = {"pathContext":{"frontmatter":{"path":"docs/flex","title":"Flex Basis, Grow, and Shrink","hasPlayground":true,"initialPlayground":null,"redirect":null},"html":"<h2>Flex Basis, Grow, and Shrink</h2>\n<p><strong>FLEX GROW</strong> describes how any space within a container should be distributed\namong its children along the main axis. After laying out its children, a container will\ndistribute any remaining space according to the flex grow values specified by its children.</p>\n<p>Flex grow accepts any floating point value >= 0, with 0 being the default value.\nA container will distribute any remaining space among its children weighted by the child’s flex grow value.</p>\n<p><controls prop=\"flexGrow\"></controls></p>\n<p><strong>FLEX SHRINK</strong> describes how to shrink children along the main axis in the\ncase that the total size of the children overflow the size of the container on the main axis.\nflex shrink is very similar to flex grow and can be thought of in the same way if\nany overflowing size is considered to be negative remaining space.\nThese two properties also work well together by allowing children to grow and shrink as needed.</p>\n<p>Flex shrink accepts any floating point value >= 0, with 1 being the default value.\nA container will shrink its children weighted by the child’s flex shrink value.</p>\n<p><controls prop=\"flexShrink\"></controls></p>\n<p><strong>FLEX BASIS</strong> is an axis-independent way of providing the default size of an item\nalong the main axis. Setting the flex basis of a child is similar to setting the <code>width</code> of that\nchild if its parent is a container with <code>flex direction: row</code> or setting the <code>height</code> of a child\nif its parent is a container with <code>flex direction: column</code>. The flex basis of an item is the\ndefault size of that item, the size of the item before any flex grow and flex shrink\ncalculations are performed.</p>\n<p><controls prop=\"flexBasis\"></controls></p>"}}

